@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}


body {
  margin: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}
a {
  color: lightgray;
}



.App {
  text-align: left;
  padding: 20px;;
}
.won-card {
  background-color: #4da375;
}

.lost-card {
  background-color: #a34d5d;
}



.MatchDetailCard {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    margin-bottom: 20px;;
}
.MatchDetailCard .match-date, .match-venue, .match-result {
    margin-top: 10px;
}
.MatchDetailCard .additional-detail {
    text-align: right;
    
}
.MatchDetailCard .additional-detail p {    
  margin-bottom: 20px;;
}

.MatchSmallCard {
    padding: 10px;
    margin-bottom: 20px;;   
}
.match-result {
    margin-top: 10px;
}
.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;


    
}

.TeamPage .team-name-section {
    grid-column: span 3;
    justify-self: start;
    align-self: end;
}

.TeamPage .team-name {
    font-size: 4rem;
    margin-bottom: 20px;
}

.TeamPage .win-loss-section {
    justify-self: center;
    padding: 40px;
    text-align: center;
}

.TeamPage .match-detail-section {
    grid-column: span 4;
}

.TeamPage .more-link {
    align-self: center;
    justify-self: center;
    font-size: 1.5rem;

}
.MatchPage {
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 20px;
    
}
.year-selector {
    justify-self: end;
    padding: 20px;
}

.page-heading {
    margin: 20px auto;
}
.YearSelector {
    text-align: right;
    list-style-type: none;
    

}
li {
    margin-top: 10px;
    margin-bottom: 10px;
}
 
.app-name {
    font-size: 3rem;
    margin: 30px auto;
}
.team-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.TeamTile {
    height: 170px;
    text-align: center;
    border: 1px solid white;
    padding: 20px;

}
